/* You can add global styles to this file, and also import other style files */
@import "~@angular/material/prebuilt-themes/indigo-pink.css";
@font-face{
    font-family: Montserrat;
    src:url('assets/fonts/Montserrat-Regular.otf');
}
@font-face{
    font-family: Montserrat-SemiBold;
    src:url('assets/fonts/Montserrat-SemiBold.otf');
}
@font-face{
    font-family: Montserrat-Bold;
    src:url('assets/fonts/Montserrat-Bold.otf');
}
@font-face{
    font-family: Montserrat-Medium;
    src:url('assets/fonts/Montserrat-Medium.otf');
}
@font-face{
    font-family: Montserrat-Black;
    src:url('assets/fonts/Montserrat-Black.otf');
}
@font-face{
    font-family: Montserrat-ExtraBold;
    src:url('assets/fonts/Montserrat-ExtraBold.otf');
} 

body{
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 400;
    background: white;
    
}
.block-app{
    width: 100%;
    height: 100vh;
    overflow: hidden;
    position: relative;
}
.mat-select-arrow{
    width: 14px !important;
    height: 6px !important;
    border: none !important;
    background: url('assets/images/ico_down.png') !important;
    background-position: center !important;
    background-size: cover !important;
    background-repeat: no-repeat !important;
}

#alerthorizontal{
    display: none;
    width: 100%;
    height: 100vh;
    align-items: center;
    justify-content: center;
    padding: 30px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9999;
    background: white;

    & img{
        max-height: 100%;
    }
}
.owl-dots{
    display: block !important;
}
.owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:hover span{
    background: #2f6aa9 !important;
}
.owl-theme .owl-dots .owl-dot span{
    background: #a6bed8 !important;
}